  src: url("../fonts/Raleway-Medium.woff2") format("woff2"), url("../fonts/Raleway-Medium.woff") format("woff");
  font-weight: 500;
  font-style: normal;
  font-display: swap; }

@font-face {
  font-family: "Raleway";
  src: url("../fonts/Raleway-SemiBold.woff2") format("woff2"), url("../fonts/Raleway-SemiBold.woff") format("woff");
  font-weight: 600;
  font-style: normal;
  font-display: swap; }

@font-face {
  font-family: "Raleway";
  src: url("../fonts/Raleway-Bold.woff2") format("woff2"), url("../fonts/Raleway-Bold.woff") format("woff");
  font-weight: 700;
  font-style: normal;
  font-display: swap; }

@font-face {
  font-family: "Raleway";
  src: url("../fonts/Raleway-ExtraBold.woff2") format("woff2"), url("../fonts/Raleway-ExtraBold.woff") format("woff");
  font-weight: 800;
  font-style: normal;
  font-display: swap; }

*,
*::after,
*::before {
  box-sizing: inherit; }

html {
  box-sizing: border-box; }

body {
  min-width: 360px;
  margin: 0;
  padding: 0;
  font-style: normal;
  font-weight: 500;
  font-size: 17px;
  line-height: 25px;
  font-family: "Raleway", "Arial", sans-serif;
  color: #e6e6e6; }

img {
  max-width: 100%;
  height: auto; }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden; }

.page-header {
  background-color: #000; }

.page-header__wrapper {
  display: flex;
  justify-content: flex-end;
  align-items: center;
  flex-wrap: wrap;
  width: 1366px;
  margin: 0 auto;
  padding-top: 23px;
  padding-bottom: 32px;
  padding-left: 32px;
  padding-right: 32px; }

.page-header__logo {
  margin-right: auto; }

.page-header__nav {
  margin-bottom: 9px;
  margin-right: 91px; }

.page-header__town {
  position: relative;
  margin-right: 25px;
  margin-bottom: 7px;
  padding: 0;
  padding-top: 6px;
  padding-bottom: 6px;
  font: inherit;
  font-size: 12px;
  line-height: 14px;
  font-weight: 700;
  letter-spacing: 0.02em;
  color: #f2890f;
  border: none;
  background: none;
  text-transform: uppercase;
  cursor: pointer; }
  .page-header__town::before {
    content: "";
    position: absolute;
    left: -24px;
    top: 50%;
    transform: translateY(-50%);
    width: 16px;
    height: 21px;
    background-image: url("../img/marker.svg");
    background-repeat: no-repeat; }
  .page-header__town::after {
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 2px;
    background-image: url("../img/line-dashed.svg");
    background-repeat: repeat-x;
    background-size: cover; }
  .page-header__town:hover::after {
    content: ""; }

.page-header__phone {
  margin-right: 7px;
  margin-bottom: 7px;
  font: inherit;
  font-size: 14px;
  line-height: 16px;
  font-weight: 600;
  letter-spacing: 0.03em;
  text-decoration: none;
  font-variant-numeric: lining-nums;
  font-feature-settings: 'lnum' 1;
  color: #f0f0f0; }
  .page-header__phone:hover, .page-header__phone:focus {
    color: #f2890f; }

.site-list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  flex-wrap: wrap; }

.site-list__item {
  margin-right: 48px; }

.site-list__link {
  font: inherit;
  font-weight: 600;
  font-size: 14px;
  line-height: 16px;
  text-transform: uppercase;
  text-decoration: none;
  letter-spacing: 0.03em;
  color: #f0f0f0; }
  .site-list__link:hover, .site-list__link:focus {
    color: #f2890f; }
